






















































































































































@import "@/assets/styles/variables.sass";
.added {
  color: white; }

.planet {
  transition: all 1200ms;
  &.hidePlanet {
    transition: all 1200ms;
    transform: translateX(-1000px); } }
.guibox {
  align-items: center;
  .planet {
    width: 100%;
    margin-bottom: 4em; }
  ul.levels {
    display: block;
    margin-bottom: 0;
    display: flex;
    margin-bottom: 1em;
    li {
      border: 1px solid #ffffff80;
      border-radius: 3px;
      padding: .6em .6em;
      margin-right: .5em;
      font-size: .8rem;
      line-height: 1; } }
  .teacher {
    margin-bottom: 1em;
    strong {
      color: #fff; }
    span {
      &::after {
        content: ', '; }
      &:last-child::after {
        content: ''; } } }
  .competences {
    font-weight: bold;
    margin-bottom: 2em; }
  .info {
    text-align: left;
    max-height: 90%;
    overflow-x: hidden;
    overflow-y: auto; }
  h1 {
    text-transform: capitalize;
    color: $teal;
    font-weight: bold;
    font-size: 1.3em;
    margin-bottom: .5em; } }
