












































































































































@import "@/assets/styles/variables.sass";
$planet: 2.5vh;
$sun: 10vh;

@keyframes rotating {
  from {
    transform: rotate3d(0,0,1,0deg); }
  to {
    transform: rotate3d(0,0,1,360deg); } }

@keyframes rotating2 {
  from {
    transform: rotate3d(0,0,1,0deg); }
  to {
    transform: rotate3d(0,0,1,-360deg); } }


.info {
  strong {
    color: #fff; }
  background: $space-blue;
  font-size: 1.5rem;
  position: fixed;
  top: 1.5rem;
  right: 1.5rem;
  color: #fff;
  text-transform: capitalize; }

.sun {
  z-index: 20;
  img {
    width: $sun;
    height: auto; } }

.planet {
  transition: border 200ms;
  transform: translateY(-.9vh) rotate(130deg);
  width: $planet;
  height: $planet;
  border-radius: $planet;
  transition: all 200ms;
  &:hover {
    transition: all 200ms;
    box-shadow: 0px 0px 34px #fea2fd;
    cursor: pointer; } }
.planet.inactive {
    box-shadow: 0px 0px 20px #FFF !important;
    border: 1.5px lightblue solid;
    transform: translateY(-.9vh) rotate(130deg) scale(1.2); }

.planet-orbit {
  position: absolute;
  display: flex;
  align-items: center;
  will-change: transform;
  transform: translateY(0);
  // border: 1px solid #ffffff20
  .orbit-wrap {
    // border: 1px red solid
    display: flex;
    justify-content: center;
    border-radius: 100%; } }

.orbit-circle {
  background: url('../assets/gfx/circle.svg');
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  z-index: 0;
  opacity: .25;
  &:nth-child(3) {
    opacity: .4; }
  &:nth-child(4) {
    opacity: .3; } }

$orbit: 99vh;
$orbit-start: 6vh;
$rotationtime: 8s;
@for $i from 2 through 17 {
  $orbit-start: $orbit-start + 7.5vh;
  $rotationtime: $rotationtime + 32s;
  .planet-orbit:nth-child(#{$i}) {
    animation: rotating $rotationtime linear infinite;
    z-index: 20 - $i;
    border-radius: $orbit-start / 2;
    .orbit-wrap {
      transform: translateY(0);
      width: $orbit-start;
      height: $orbit-start;
      a {
        z-index: 20 - $i; } } } }
$orbital: 6vh;
@for $i from 3 through 18 {
  $orbital: $orbital + 7.5vh;
  .orbit-circle:nth-child(#{$i}) {
    height: $orbital;
    width: $orbital; } }

.guibox.message {
  position: fixed;
  width: 40vw;
  height: auto;
  z-index: 999;
  padding: 30px;
  right: 2vw;
  top: 5vw;
  margin: 0;
  // transform: translate(-50%, -50%)
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: left;
  padding: 1.5em 2em 2em 2em;
  strong {
    color: $teal;
    text-transform: uppercase;
    font-size: 1.1em;
    margin-bottom: 1em;
    display: block; }
  p {
    line-height: 1.2em; } }

.spacestation {
  position: fixed;
  bottom: 2em;
  right: 4em;
  animation: rotating2 90s linear infinite;
  transform-origin: center center;
  transition: all 250ms;
  &:hover {
    transform: scale(2); } }
