

















































































































@import "@/assets/styles/variables.sass";
.guibox {
  text-align: left;
  padding: 1.5rem;
  // overflow-x: scroll
  .progresschart {
    // height: 60%
    width: auto;
    margin-bottom: .5em;
    grid-column-gap: 5.5%;
    padding: 0 0 0 15%;
    border-bottom: 1px solid #fff;
    .bar {
      border: 1px solid #fff; } }
  .competences {
    display: flex;
    justify-content: space-around;
    padding-left: 8%;
    margin-bottom: 2em;
    font-size: .8em;
    font-weight: bold; } }
.text {
  h3 {
    font-family: $A;
    font-size: 1.125rem;
    text-transform: uppercase;
    margin-bottom: 1em; }
  p {
    font-size: .875rem;
    margin-bottom: 2em; } }


.chart-legend {
  padding: 0 8% 0 13%;
  // background: red
  .inner {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1em;
 }    // padding: 0 5.5% 0 3.5%
  .letter {
    padding: 1px 8px 0;
    border-radius: 5px;
    border: 1px transparent solid; }
  .letter.current {
    font-weight: bold;
    border: 1px white solid; } }

.competence-names {
  font-size: .8em;
  line-height: 1.4;
  color: #BBB;
  li {
    padding-bottom: 0.7em; }
  li::before {
    font-weight: bold; }
  li:nth-child(1)::before {
    content: 'A:  '; }
  li:nth-child(2)::before {
    content: 'B:  '; }
  li:nth-child(3)::before {
    content: 'C:  '; }
  li:nth-child(4)::before {
    content: 'D:  '; }
  li:nth-child(5)::before {
    content: 'E:  '; } }

.lernlevel-info {
  margin-top: 2em;
  color: #BBB;
  font-size: .8em; }
