



































































































































































































































































































































































































@import "@/assets/styles/variables.sass";


$guiH: calc(100vh - 100px);
// $levelNav: 16vh + margin-bottom: 4vh
$wrapH: calc(80vh - 108px);
$contentH: calc(80vh - 139px - 3rem);

.addButtonContainer * {
  float: right; }

.guibox {
  text-align: left;
  padding: 1.5em;
  padding-bottom: 0;
  overflow-x: hidden;
  // overflow-y: auto
  overflow-y: hidden;
  // div
  // border: 1px red solid
  height: $guiH; }

.guibox.spacestationgui {
  height: calc(87vh - 100px); }

.spacestationspacer {
  height: 20px; }

.under-level-nav {
  // background: blue
  // height: calc(77vh - 100px)
  // height: calc(77vh - 86px)
  height: $wrapH; }

.no-mb {
  margin-bottom: 0 !important; }

.side-menu {
  font-family: $A;
  // font-size: 1.125em
  font-size: 1.1em;
  padding-right: 1.5em;
  padding-bottom: 0;
  position: relative;
  overflow-y: auto;
  // height: calc(70vh - 100px - 3rem)
  ul {
    border-top: 1px solid $lightgrey;
    padding-top: 1em;
    li {
      margin-bottom: 1.2em;
      padding-bottom: 1.2em;
      border-bottom: 1px solid $lightgrey;
      line-height: 1.2;
      a {
        color: #fff;
        &:hover, &.router-link-active {
          color: $lightgrey; } } }
    .section-title {
      font-family: $R;
      color: $teal;
      font-size: .8rem;
      line-height: 1;
      position: absolute;
      margin-top: -2.1em;
      background: $darkgrey;
      font-weight: bold;
      padding: 0 .5em 0 0;
      border: none; } } }
.main {
  padding-bottom: 0;
  .tabs {
    a {
      color: #fff; }
    .txt {
      // background: red
      padding: 0 1em;
      opacity: 0.5;
      cursor: no-drop; }
    ul {
      border-color: $teal;
      li.is-active a, li a:hover {
        background: #0a0f26;
        border-color: $teal; } } }
  h2 {
    font-size: 1.3rem;
    font-weight: bold;
    color: #fff;
    margin-bottom: .75em; }
  .tab-content {
    overflow-x: hidden;
    // max-height: calc(70vh - 100px - 2rem)
    max-height: $contentH;
    // background: red
    &.info {
 }      // padding-right: 2em
    p.description {
      margin-bottom: 2em; }
    strong {
      color: #fff; } }
  .material {
    .materialLink:hover {
      cursor: pointer;
      li {
        background-color: #70F3D220; } }
    li {
      &:hover {
        cursor: pointer; }
      font-family: $A;
      display: flex;
      padding: 1em;
      border-radius: 10px;
      .text {
        max-width: 80%; }
      .title {
        font-size: 1.1rem;
        line-height: 1.2em;
        margin-bottom: 0.5em;
        margin-top: 0.2em;
        color: #fff;
        text-transform: uppercase; }
      p {
        font-size: .8rem;
        color: #AAA; }
      &::before {
        content: '';
        height: auto;
        display: block;
        margin-right: 1.5em; } }

    li[data-type='Link']::before {
      background: url('../assets/gfx/icons/link_icon.svg');
      background-repeat: no-repeat;
      background-size: contain;
      width: 60px;
      height: 60px; }
    li[data-type='PDF']::before {
      background: url('../assets/gfx/icons/text_document_icon.svg');
      background-repeat: no-repeat;
      background-size: contain;
      width: 60px;
      height: 60px; }
    li[data-type='Video']::before, li[data-type='Bild']::before {
      background: url('../assets/gfx/icons/rich_media_icon.svg');
      background-repeat: no-repeat;
      background-size: contain;
      width: 60px;
      height: 60px; } } }
