





































































































.user-menu {
  position: fixed;
  bottom: 20px;
  left: 20px;
  // &:hover .btn.top
  //   top: -45px
  // &:hover .btn.add
  //   right: -45px
  .btn {
    position: absolute;
    width: 30px;
    height: auto;
    transition: all 200ms;
    z-index: 1;
    &:hover {
      opacity: .7; }
    &.top, &.settings {
      // top: 0px
      left: 35px;
      top: -45px; }
    &.add {
      top: 35px;
      right: -45px;
 } }      // right: 0
  .avatar {
    .avatarImg-wrapper {
      height: 90px;
      width: 90px;
      overflow: hidden;
      border-radius: 90px; }
    .avatarImg {
      height: 90px;
      width: auto !important;
      max-width: none; }
    width: 100px;
    height: 100px;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50px;
    z-index: 10;
    position: relative;
    // blue
    &[data-color="blue"] {
      background: #4A90E2; }
    // orange
    &[data-color="orange"] {
      background: #F5A623; }
    // yellow
    &[data-color="yellow"] {
      background: #F8E71C; }
    // lightgreen
    &[data-color="lightgreen"] {
      background: #7ED321; }
    // darkgreen
    &[data-color="darkgreen"] {
      background: #417505; }
    // purple
    &[data-color="purple"] {
      background: #BD10E0; }
    // pastel teal
    &[data-color="pastelteal"] {
      background: #77C3B2; }
    // pastel orange
    &[data-color="pastelorange"] {
      background: #FFD185; }
    // pastel pink
    &[data-color="pastelpink"] {
      background: #FFB8B9; }
    // pastel green
    &[data-color="pastelgreen"] {
      background: #CBE5B1; }
    // pastel red
    &[data-color="pastelred"] {
      background: #FF5F73; }
    // pastel purple
    &[data-color="pastelpurple"] {
      background: #D3A1FF; } } }
