











































































































@import "@/assets/styles/variables.sass";
.padfix {
  margin-bottom: 0.7em; }
.lorem {
  height: 6em;
  overflow: hidden; }
#levelnav {
  font-size: .8em;
  margin-bottom: 4vh;
  // background: red
  height: 16vh;
  // position: absolute
  overflow: hidden;
  a {
    color: #fff; }
  .chart {
    padding-bottom: 0; }
  .desc {
    .level {
      font-weight: bold; } }
  .themen-link {
    font-weight: bold;
    display: flex;
    align-items: center;
    // border-top-right-radius: .8rem
    border-top-right-radius: 0.3rem;
    transition: all, 200ms;
    background: darken($teal, 10);
    text-shadow: 0.5px 0.5px 3px darken($teal, 25);
    border: 1px solid $teal;
    margin-bottom: 1em;
    // margin-bottom: 1.8em
    padding-left: 2em; }
  .themen-link.is-not-lit {
    background: none;
    border: 1px solid #fff;
    &:hover {
      background: $teal;
      border: 1px solid $teal; } } }
.progresschart {
  padding: 0;
  background-position: 12% 100%;
  .bar {}
  .competence-names {
    display: none; } }
.levelsWrapper {
  padding: 1em 0 0 2em;
  .inner {
    max-width: 768px; } }
