












































































































































































#lernlevel {
  position: relative; }
img.lernlevelimg {
  width: 40vw !important;
 }  // border: 1px white solid
.select-level {
  // border: 1px white solid
  position: absolute;
  margin-top: -4px;
  transform: translateY(-100%);
  height: 100%;
  width: 40vw !important;
  display: flex;
  justify-content: space-between;
  align-items: center;
  button {
    height: 2vw;
    width: 2vw;
    border-radius: 5vw;
    border: 1px solid transparent;
    background: none;
    outline: none;
    transition: border 200ms;
    &:last-child {
      margin-right: 0; }
    &:hover, &.is-selected {
      border: 2px solid #fff;
      cursor: pointer; } } }

