



















































































































































































@import "@/assets/styles/variables.sass";
$orbit: 30vh;
$planet: 25vh;
$moon: 4vh;
$moonFix: 4.2vh;

@keyframes rotating {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

.info {
  background: $space-blue;
  strong {
    color: #fff; }
  position: fixed;
  font-size: 1.3rem;
  top: 1.5rem;
  right: 1.5rem;
  text-align: right;
  color: #fff;
  text-transform: capitalize; }

.planet {
  border-radius: $planet / 2;
  width: $planet;
  height: $planet;
  transition: box-shadow 400ms ease;
  z-index: 2;
  &[data-levels="BK"] {
    box-shadow: 0px 0px 3vh #fff; }
  &[data-levels="GK"] {
    box-shadow: 0 0 0 .5vh #fff, 0 0 0 2vh #9DB7D6; }
  &[data-levels="AK1"], &[data-levels="AK"] {
    box-shadow: 0 0 0 .5vh #fff, 0 0 0 2vh #9DB7D6, 0 0 0 4vh #7397C1; }
  &[data-levels="AK2"] {
    box-shadow: 0 0 0 .5vh #fff, 0 0 0 2vh #9DB7D6, 0 0 0 4vh #7397C1, 0 0 0 6vh #5477A1; }
  img {
    width: $planet;
    height: $planet; } }

.hideMoons {
  transition: all 500ms;
  opacity: 0; }


.levels {
  text-transform: uppercase;
  position: fixed;
  bottom: 2em;
  right: 2em;
  display: flex;
  font-size: .815rem;
  a {
    color: #fff; }
  line-height: .815;
  li {
    margin-left: .5em;
    border: 1px solid #fff;
    border-radius: 3px;
    padding: .6em;
    &.is-active {
      border: 1px solid $teal;
      a {
        color: $teal; } }
    // &.is-active ~ li
    &.levelUnavailable {
      border: 1px solid $lightgrey;
      cursor: no-drop;
      a {
        color: $lightgrey; } }
    .levelUnavailable {} } }

.orbit-circle {
  height: ($orbit*2) - $moon;
  width: ($orbit*2) - $moon;
  border-radius: (($orbit*2) - $moon)*2;
  border: 1px dashed $lightgrey;
  position: absolute;
  z-index: 0; }

.orbit-wrap {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: - $orbit / 2;
  @media screen and (min--moz-device-pixel-ratio:0) {
    margin-top: - $orbit; }
  animation: rotating 200s linear infinite;
  transform-origin: 0 ($orbit / 2);
  .orbit {
    height: $orbit;
    position: absolute;
    transform-origin: bottom;
    transition: all 500ms;
    .moon {
      width: $moonFix;
      height: $moon;
      border-radius: $moon/2;
      &:hover {
        box-shadow: 0px 0px 24px #fea2fd;
        cursor: pointer; } } } }

$rotation: 0;

@for $ii from 1 through 12 {
  .moonN_#{$ii} {
    @for $i from 1 through $ii {
      $rotation: $rotation + (360deg / $ii);
      .orbit:nth-child(#{$i}) {
        transform: rotate($rotation); } } } }

.guibox.message {
  position: fixed;
  width: 40vw;
  height: auto;
  z-index: 999;
  padding: 30px;
  left: 50vw;
  bottom: 3vh;
  margin: 0;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: left;
  padding: 1.5em 2em 2em 2em;
  .msg {
    width: 100%; }
  strong {
    color: $teal;
    text-transform: uppercase;
    font-size: 1.1em;
    margin-bottom: 0.5em;
    display: block; }
  p {
    line-height: 1.2em; } }
