.avatarImg-wrapper {
  overflow: hidden;
  width: 22vw;
  height: 22vw;
  border-radius: 11vw; }

.builtin {
  height: 22vw !important;
  // width: 22vw !important
  width: auto !important;
  max-width: none;
  border-radius: 11vw; }

.uploadingImg {
  width: 22vw;
  height: 22vw;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #CCC !important;
  strong, i {
    color: #CCC !important; } }

.avatar-uploader {
  position: fixed;
  width: 27vw;
  height: 27vw;
  border-radius: 13.5vw;
  // &.hide
 }  //// opacity: 0 !important
.guibox {
  text-align: left;
  overflow-x: hide; }
.left-zone {
  border-right: 2px solid $teal;
  .avatar-grid {
    border-bottom: 2px solid $teal;
    padding: 1rem; }
  .avatar {
    .image {
      background: $grey;
      border-radius: 10px;
      padding: .5rem;
      &:hover {
        background: $lightgrey;
        cursor: pointer; } } }
  .colors {
    padding: 1rem;
    .color {
      display: flex;
      align-items: center;
      .dot {
        height: 3.5vw;
        width: 3.5vw;
        border-radius: 3.5vw;
        &:hover {
          border: 2px solid #fff;
          cursor: pointer; } } } } }
.right-zone {
  // padding: 0 3rem
  display: flex;
  align-items: center;
  justify-content: center;
  .flex-wrap {
    input {
      border: none;
      padding: .9em;
      line-height: 1;
      border-radius: 1em;
      width: 100%;
      margin-bottom: 1em;
      font-size: .8rem; }
    .avatar-wrap {
      padding: 2rem;
      .avatar {
        // width: 300px
        // height: 300px
        border-radius: 50%;
        padding: 1em;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        img {
          width: 100%;
          // width: 80%
          height: auto; } } }
    .graphic .avatar {
      padding: 1em !important;
      img {
        border-radius: 0; } }

    .buttons {
      display: flex;
      justify-content: center;
      .button {
        font-size: .815rem;
        margin: 1em;
        border-radius: .5em; } } } }
.blue {
  background: #4A90E2; }
.orange {
  background: #F5A623; }
.yellow {
  background: #F8E71C; }
.lightgreen {
  background: #7ED321; }
.darkgreen {
  background: #417505; }
.purple {
  background: #BD10E0; }
.pastelteal {
  background: #77C3B2; }
.pastelorange {
  background: #FFD185; }
.pastelpink {
  background: #FFB8B9; }
.pastelgreen {
  background: #CBE5B1; }
.pastelred {
  background: #FF5F73; }
.pastelpurple {
  background: #D3A1FF; }
.grassgreen {
  background: #97c963; }

.form {
  p {
    margin: 1em 0 0.5em; }
  p.center {
    margin-top: 1.5em; } }

