




































































































@import "@/assets/styles/variables.sass";

.progresschart {
  padding-left: 20px;
  // height: 100%
  width: 100%;
  // min-height: 7em
  min-width: 20px;
  &.small {
    height: 13vh;
    .bars {
      // background: url('../../assets/gfx/graphs/chart-lines.svg')
      background: url('../../assets/gfx/graphs/chart-lines-small.svg');
      background-repeat: no-repeat;
      background-size: cover;
      // padding-right: 8%
      // background-position: -1em 0
      max-width: 90px;
      border-bottom: 1px white solid;
 }      // border-left: 1px white solid
    .bar .inner {
      height: 13vh; } }
  &.large {
    height: 33vh;
    background: url('../../assets/gfx/graphs/chart-lines.svg');
    background-repeat: no-repeat;
    background-size: cover;
    .bars {
      padding-right: 10%; }
    .bar .inner {
      height: 33vh; } } }
#levelnav {
  .tab:hover .progresschart .bar .inner {
      background-image: linear-gradient(-180deg, $teal 0%, $teal 58%); }
  .tab:hover .progresschart .bars {
    // background: url('../../assets/gfx/graphs/chart-lines-teal.svg')
    background: url('../../assets/gfx/graphs/chart-lines-small-teal.svg');
    background-repeat: no-repeat;
    background-size: cover;
    // background-position: 7% 100%
 } }    // background-position: -1em 0
#leveView {
  .progresschart {
    height: 90px; } }

.bars {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  height: 100%;
  // padding: 0 7% 0 5%
 }  // padding: 0 1% 0 0%
.bar {
  width: 12.5%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  transition: all 200ms ease;
  display: flex;
  align-items: flex-end;
  .inner {
    width: 100%;
 }    // height: 100%
  &:hover {
    opacity: .8;
    cursor: pointer; }


  &.large {
    border: 1px white solid; } }

.levels-3 {
  .bars.BK .bar .inner {
    background-image: linear-gradient(180deg, #DC1F32 0%, #FCB814 100%); }
  .bars.GK .bar .inner {
    background-image: linear-gradient(180deg, #653393 0%, #ED1D25 100%); }
  .bars.AK .bar .inner {
    background-image: linear-gradient(180deg, #0B663D 0%, #66BAE6 50%, #653494 100%); } }

.levels-4 {
  .bars.BK .bar .inner {
    background-image: linear-gradient(180deg, #DC1F32 0%, #FCB814 100%); }
  .bars.GK .bar .inner {
    background-image: linear-gradient(180deg, #653393 0%, #ED1D25 100%); }
  .bars.AK1 .bar .inner {
    background-image: linear-gradient(180deg, #66BEE9 0%, #653393 100%); }
  .bars.AK2 .bar .inner {
    background-image: linear-gradient(180deg, #0B663D 0%, #47A7D6 100%); } }

